import React, { useContext } from "react";

import styles from "./Exercises.module.css";
import lock from "../../assets/default/lock.svg";
import list from "../../assets/default/list.svg";

import audio from "../../sound/game.wav";
import { MainContext } from "../../context/Context";
import { get_list } from "../../actions/ExerciseAction";
import { Link } from "react-router-dom";
export const ListExercises = () => {
  const { state, dispatch } = useContext(MainContext);
  React.useEffect(() => {
    get_list(dispatch);
  }, []);

  React.useEffect(() => {
    var snd = new Audio(audio); // buffers automatically when created
    snd.play();
  }, []);

  return (
    <div className={styles.backgroundimage}>
      <div className={styles.container}>
        <h1 className={styles.title}>Exercises</h1>
        <div className={styles.boxlistMaster}>
          {state.list_exercise &&
            state.list_exercise.map((content, index) => (
              <Link
                onClick={() => {
                  localStorage.setItem("LISTID", content.id);
                }}
                to={`${content.slug}`}
                key={index}
                className={`${styles.boxlist} ${
                  content.availability ? "" : styles.block
                }`}
              >
                <img width="15px" src={list} />
                <p>{content.list_name}</p>
                {content.availability ? (
                  <p className={styles.score}>100</p>
                ) : (
                  <img width="15px" src={lock} />
                )}
              </Link>
            ))}
        </div>
      </div>
    </div>
  );
};
