import React, { useContext } from "react";
import { MainContext } from "../../context/Context";
import { Interface } from "../interface/Interface";
import styles from "./Home.module.css";

import comunidade from "../../assets/default/comunidade.svg";
import conteudo from "../../assets/default/conteudo.svg";
import games from "../../assets/default/games.svg";
import desafio from "../../assets/default/desafio.svg";
import torneio from "../../assets/default/torneio.svg";
import exercicio from "../../assets/default/exercicio.svg";
import { Link } from "react-router-dom";
function Home() {
  const { state, dispatch } = useContext(MainContext);

  React.useEffect(() => {}, []);
  return (
    <div className={styles.container}>
      <div className={styles.boxfixed}>
        <Link to="/community">
          <h4>Community</h4>
          <div className={styles.boxContent}>
            <img src={comunidade} />
          </div>
        </Link>
        <Link to="/content">
          <h4>Content</h4>
          <div className={styles.boxContent}>
            <div className={styles.notification}></div>
            <img src={conteudo} />
          </div>
        </Link>

        <div className={styles.containerActive}>
          <Link to="/games">
            <h4>Games</h4>
            <div className={styles.boxContent}>
              <img src={desafio} />
              <div
                style={{ backgroundColor: "rgb(187, 247, 192)" }}
                className={styles.progressbar}
              >
                <div
                  style={{ width: "20%", backgroundColor: "#7ddd10e5" }}
                  className={styles.progressbarElement}
                ></div>
              </div>
            </div>
          </Link>
          <Link to="/challenge">
            <h4>Challenges</h4>
            <div className={styles.boxContent}>
              <img src={games} />
              <div
                style={{ backgroundColor: "#73B5F6" }}
                className={styles.progressbar}
              >
                <div
                  style={{ width: "60%", backgroundColor: "#1077DD" }}
                  className={styles.progressbarElement}
                ></div>
              </div>
            </div>
          </Link>
          <Link to="/tournament">
            <h4>Tournament</h4>
            <div className={styles.boxContent}>
              <img src={torneio} />
              <div
                style={{ backgroundColor: "#AF73CE" }}
                className={styles.progressbar}
              >
                <div
                  style={{ width: "10%", backgroundColor: "#7B1DAA" }}
                  className={styles.progressbarElement}
                ></div>
              </div>
            </div>
          </Link>
          <Link to="/exercises">
            <h4>Exercises</h4>
            <div className={styles.boxContent}>
              <img src={exercicio} />
              <div
                style={{ backgroundColor: "#E9C372" }}
                className={styles.progressbar}
              >
                <div
                  style={{ width: "20%", backgroundColor: "rgb(221, 156, 16)" }}
                  className={styles.progressbarElement}
                ></div>
              </div>
            </div>
          </Link>
        </div>
      </div>
    </div>
  );
}

export default Home;
