import React, { useContext } from "react";
import { ProfileNav } from "./ProfileNav";

import styles from "./Profile.module.css";
import user from "../../assets/profile/profile.png";
import write from "../../assets/default/write.svg";
import { AuthContext } from "../../reducers/Auth";

export const ProfileEdit = () => {
  const { state, dispatch } = useContext(AuthContext);

  return (
    <div className={styles.container}>
      <ProfileNav />
      <div className={styles.anima}>
        <h2>Information User</h2>
        <div className={styles.containerEdit}>
          <img
            style={{ borderRadius: "9px" }}
            width="100px"
            src={state.user && state.user.photo}
          />
          <div style={{ textAlign: "center" }}>
            <p>{state.user && state.user.nickname}</p>
          </div>
          <div className={styles.data}>
            <p>{state.user && state.user.username}</p>
            <p>{state.user && state.user.matriculation}</p>
            <p>{state.user && state.user.email}</p>
          </div>
        </div>
      </div>
    </div>
  );
};
