import axios from "axios";

import {
  LOGIN_SUCCESS,
  LOGIN_USER_FAIL,
  LOAD_USER_SUCCESS,
  LOAD_USER_FAIL,
  USER_AUTHENTICATED_SUCCESS,
  USER_AUTHENTICATED_FAIL,
  LOGOUT,
  PASSWORD_RESET_FAIL,
  PASSWORD_RESET_SUCCESS,
  CONFIRM_RESET_PASSWORD_FAIL,
  CONFIRM_RESET_PASSWORD_SUCCESS,
  ACTIVATION_SUCCESS,
  ACTIVATION_FAIL,
  LOADING,
  LOADING_FAIL,
  RE_SEND_ACTIVATION,
  RE_SEND_ACTIVATION_FAIL,
  ALTER_PROFILE,
  ALTER_PROFILE_FAIL,
} from "./types";

const URL = process.env.REACT_APP_URL;
// const URL = "https://questionx.herokuapp.com";

export const load_user = async (dispatch) => {
  if (localStorage.getItem("access")) {
    const config = {
      headers: {
        "Content-Type": "application/json",
        Authorization: `JWT ${localStorage.getItem("access")}`,
        Accept: "application/json",
      },
    };
    try {
      const res = await axios.get(URL + "/auth/users/me/", config);
      dispatch({ type: LOAD_USER_SUCCESS, payload: res.data });
    } catch (err) {
      dispatch({ type: LOAD_USER_FAIL });
    }
  } else {
    dispatch({ type: LOAD_USER_FAIL });
    console.log("DEU ERRADO");
  }
};

export const verify = (uid, token) => async (dispatch) => {
  const config = {
    headers: {
      "Content-Type": "application/json",
    },
  };

  const body = JSON.stringify({ uid, token });

  const res = await axios.post(URL + "/auth/users/activation/", body, config);

  try {
    dispatch({
      type: ACTIVATION_SUCCESS,
      payload: res.data,
    });
  } catch (err) {
    dispatch({ type: ACTIVATION_FAIL });
  }
};

export const alter = (username, email, password) => async (dispatch) => {
  dispatch({ type: LOADING });
  const config = {
    headers: {
      "Content-Type": "application/json",
      Authorization: `JWT ${localStorage.getItem("access")}`,
    },
  };
  const body = JSON.stringify({
    username: username,
    email: email,
    password: password,
  });

  try {
    const res = await axios.put(URL + "/auth/users/me/", body, config);
    dispatch({ type: ALTER_PROFILE });
    dispatch({ type: LOADING_FAIL });
  } catch (err) {
    dispatch({ type: ALTER_PROFILE_FAIL });
  }
};

export const login = async (email, password, dispatch) => {
  dispatch({ type: LOADING });
  const config = {
    headers: {
      "Content-Type": "application/json",
    },
  };

  const body = JSON.stringify({ email, password });

  try {
    const res = await axios.post(URL + "/auth/jwt/create/", body, config);

    dispatch({
      type: LOGIN_SUCCESS,
      payload: res.data,
    });

    dispatch(load_user());
  } catch (err) {
    dispatch({ type: LOGIN_USER_FAIL });
    dispatch({ type: LOADING_FAIL });
  }
};

export const reset_password = (email) => async (dispatch) => {
  const config = {
    headers: {
      "Content-Type": "application/json",
    },
  };
  const body = JSON.stringify({ email });

  try {
    const res = await axios.post(
      URL + "/auth/users/reset_password/",
      body,
      config
    );

    dispatch({ type: PASSWORD_RESET_SUCCESS });
  } catch (err) {
    dispatch({ type: PASSWORD_RESET_FAIL });
  }
};

export const reset_password_confirm =
  (uid, token, new_password, re_new_password) => async (dispatch) => {
    const config = {
      headers: {
        "Content-Type": "application/json",
      },
    };

    const body = JSON.stringify({ uid, token, new_password, re_new_password });

    try {
      await axios.post(
        URL + "/auth/users/reset_password_confirm/",
        body,
        config
      );

      dispatch({
        type: CONFIRM_RESET_PASSWORD_SUCCESS,
      });
    } catch (err) {
      dispatch({
        type: CONFIRM_RESET_PASSWORD_FAIL,
      });
    }
  };

export const checkAuthenticated = async (dispatch) => {
  if (localStorage.getItem("access")) {
    const config = {
      headers: {
        "Content-Type": "application/json",
        Accept: "application/json",
      },
    };

    const body = JSON.stringify({ token: localStorage.getItem("access") });
    try {
      const res = await axios.post(URL + "/auth/jwt/verify/", body, config);

      if (res.data.code !== "token_not_valid") {
        dispatch({ type: USER_AUTHENTICATED_SUCCESS });
      } else {
        dispatch({ type: USER_AUTHENTICATED_FAIL });
      }
    } catch (err) {
      dispatch({ type: USER_AUTHENTICATED_FAIL });
    }
  } else {
    dispatch({ type: USER_AUTHENTICATED_FAIL });
  }
};

export const logout = (dispatch) => {
  dispatch({ type: LOGOUT });
};
