import React, { useContext } from "react";
import { Route, Routes } from "react-router-dom";
import { Content } from "../../template/content/Content";
import { ContentElement } from "../../template/content/ContentElement";

import styles from "../../template/content/Content.module.css";
import { MainContext } from "../../context/Context";
import { get_content } from "../../actions/ContentAction";

export const ContentRouter = () => {
  const { state, dispatch } = useContext(MainContext);

  React.useEffect(() => {
    get_content(dispatch);
  }, []);

  return (
    <div>
      <Routes>
        <Route path="content" element={<Content />} />
        {state.content &&
          state.content.map((content, index) => (
            <Route
              key={index}
              path={`content/${content.slug}`}
              element={<ContentElement content={content} />}
            />
          ))}
      </Routes>
    </div>
  );
};
