import React, { useContext } from "react";

import styles from "./Challenge.module.css";
import lock from "../../assets/default/lock.svg";
import list from "../../assets/default/list.svg";

import audio from "../../sound/game.wav";
import { MainContext } from "../../context/Context";
import { Link } from "react-router-dom";
export const Challenge = () => {
  const { state, dispatch } = useContext(MainContext);

  const [availability, setAvailability] = React.useState(true);

  React.useEffect(() => {
    var snd = new Audio(audio); // buffers automatically when created
    snd.play();
  }, []);

  return (
    <div className={styles.backgroundimage}>
      <div className={styles.container}>
        <h1 className={styles.title}>Challenges</h1>
        <div className={styles.boxlistMaster}>
          {[...Array(7)].map((content, index) => (
            <Link
              to={`d`}
              key={index}
              className={`${styles.boxlist} ${
                availability ? "" : styles.block
              }`}
            >
              <img width="15px" src={list} />
              <p>teste</p>
              {availability ? (
                <p className={styles.score}>100</p>
              ) : (
                <img width="15px" src={lock} />
              )}
            </Link>
          ))}
        </div>
      </div>
    </div>
  );
};
