import React from "react";
import styles from "./Profile.module.css";
import { ProfileNav } from "./ProfileNav";

import trofeu from "../../assets/default/trofeu.svg";
export const Profile = () => {
  return (
    <div className={styles.container}>
      <ProfileNav />
      <div className={styles.anima}>
        <div className={styles.hexagon}>
          <div className={styles.position}>25</div>
          <h1>Score</h1>
          <div className={styles.quadroScore}>
            <img width="100px" src={trofeu} />
          </div>
          <h1>200</h1>
        </div>
        <h2 style={{ marginTop: "50px", textAlign: "center" }}>Performance</h2>
        <div className={styles.containerPerformance}>
          <div className={styles.performance}>
            <h4>Games</h4>
          </div>
          <div className={styles.performance}>
            <h4>Challenges</h4>
          </div>
          <div className={styles.performance}>
            <h4>Tournament</h4>
          </div>
          <div className={styles.performance}>
            <h4>Exercises</h4>
          </div>
        </div>
      </div>
    </div>
  );
};
