import React from "react";
import axios from "axios";
import { GET_CONTENT_FAIL, GET_CONTENT_SUCCESS } from "./types";

const URL = process.env.REACT_APP_URL;

export const get_content = async (dispatch) => {
  if (localStorage.getItem("access")) {
    const config = {
      headers: {
        "Content-Type": "application/json",
        Authorization: `JWT ${localStorage.getItem("access")}`,
        Accept: "application/json",
      },
    };
    try {
      const res = await axios.get(URL + "/api/v1/content/", config);
      dispatch({ type: GET_CONTENT_SUCCESS, payload: res.data });
    } catch (err) {
      dispatch({ type: GET_CONTENT_FAIL });
    }
  } else {
    dispatch({ type: GET_CONTENT_FAIL });
    console.log("DEU ERRADO");
  }
};
