import logo from "../../assets/default/logo.svg";
import styles from "./Login.module.css";
import React, { useContext } from "react";
import { Link, Navigate } from "react-router-dom";

import { login } from "../../actions/LoginAction";
import { AuthContext } from "../../reducers/Auth";
import RingLoader from "react-spinners/ClipLoader";
// login, isAuthenticated, loading, error
export function Login() {
  const { state, dispatch } = useContext(AuthContext);
  const [formData, setFormData] = React.useState({ email: "", password: "" });
  const [errorEmail, setErrorEmail] = React.useState(null);
  const { email, password } = formData;

  function onChange(e) {
    if (e.target.value.length > 0) {
      setErrorEmail("");
    }
    setFormData({ ...formData, [e.target.name]: e.target.value });
  }
  function onSubmit(e) {
    e.preventDefault();
    login(email, password, dispatch);
  }

  function validate({ target }) {
    if (target.name === "email" && target.value.length === 0) {
      setErrorEmail("Fill the field");
    } else {
      return true;
    }
  }

  if (state.isAuthenticated === true) {
    return <Navigate to="/home" />;
  }

  document.title = "Entrar - QuestoX";

  return (
    <div className={styles.container}>
      <div className={styles.image}>
        <img width="250px" src={logo} />
      </div>
      <div className={styles.contentForms}>
        {state.error && (
          <div className={styles.errorGeralContainer}>
            <h3 className={styles.errorGeral}>{state.error && state.error}</h3>
          </div>
        )}

        <p className={styles.content}>Enter your data to access the platform</p>
        <form onSubmit={(e) => onSubmit(e)}>
          <input
            type="email"
            name="email"
            onBlur={(e) => validate(e)}
            value={email}
            autoComplete="off"
            onChange={(e) => onChange(e)}
            placeholder="Email"
            className={styles.input}
            required
          />
          {errorEmail && <p className={styles.error}>{errorEmail}</p>}
          <input
            type="password"
            placeholder="Senha"
            name="password"
            value={password}
            onBlur={(e) => validate(e)}
            autoComplete="off"
            onChange={(e) => onChange(e)}
            className={styles.input}
            required
          />
          {state.loading && state.loading ? (
            <button className={styles.button} disabled>
              <RingLoader color={"#fff9"} size={18} />
            </button>
          ) : (
            <button className={styles.button} type="submit">
              Enter
            </button>
          )}
        </form>
        <div className={styles.link}>
          <Link className={styles.links} to="/login/esqueci-senha">
            Forgot password ?
          </Link>
        </div>
      </div>
    </div>
  );
}
