import React from "react";
import { Link } from "react-router-dom";

import styles from "./Games.module.css";

import trofeu from "../../assets/default/trofeu.svg";
import audio from "../../sound/game.wav";

export const Games = () => {
  React.useEffect(() => {
    var snd = new Audio(audio); // buffers automatically when created
    snd.play();
  }, []);

  return (
    <div className={styles.container}>
      <div className={styles.containerNav}>
        <div>
          <h1 className={styles.title}>Games</h1>
          <p className={styles.message}>Time to practice what you've learned</p>
        </div>
        <Link to="classroom">
          <div className={styles.button}>start</div>
        </Link>
      </div>

      <h3 className={styles.online}>Online</h3>

      <div className={styles.containerUser}>
        {[...Array(4)].map((x) => (
          <div className={styles.user}>
            <div className={styles.imageUser}></div>
            <p className={styles.nameUser}>ALECRIN</p>
            <div className={styles.score}>
              {" "}
              <img width="17px" src={trofeu} /> 87
            </div>
          </div>
        ))}
      </div>
    </div>
  );
};
