import React from "react";
import axios from "axios";
import {
  GET_LIST_FAIL,
  GET_LIST_SUCCESS,
  GET_QUESTION_SUCCESS,
  GET_QUESTION_FAIL,
} from "./types";

const URL = process.env.REACT_APP_URL;

export const get_list = async (dispatch) => {
  if (localStorage.getItem("access")) {
    const config = {
      headers: {
        "Content-Type": "application/json",
        Authorization: `JWT ${localStorage.getItem("access")}`,
        Accept: "application/json",
      },
    };
    try {
      const res = await axios.get(URL + "/api/v1/list-exercise/", config);
      dispatch({ type: GET_LIST_SUCCESS, payload: res.data });
    } catch (err) {
      dispatch({ type: GET_LIST_FAIL });
    }
  } else {
    dispatch({ type: GET_LIST_FAIL });
    console.log("DEU ERRADO");
  }
};

export const get_question = async (dispatch, id) => {
  if (localStorage.getItem("access")) {
    const config = {
      headers: {
        "Content-Type": "application/json",
        Authorization: `JWT ${localStorage.getItem("access")}`,
        Accept: "application/json",
      },
    };
    try {
      const res = await axios.get(
        URL + `/api/v1/list-exercise/question/${id}/`,
        config
      );
      dispatch({ type: GET_QUESTION_SUCCESS, payload: res.data });
    } catch (err) {
      dispatch({ type: GET_QUESTION_FAIL });
    }
  } else {
    dispatch({ type: GET_QUESTION_FAIL });
  }
};
