import React, { useContext } from "react";
import { Route, Routes } from "react-router-dom";
import { get_list, get_question } from "../../actions/ExerciseAction";

import { MainContext } from "../../context/Context";
import { ExerciseModule } from "../../template/exercises/ExerciseModule";
import { ListExercises } from "../../template/exercises/ListExercises";
import { Interface } from "../../template/interface/Interface";

export const ExercisesRouter = () => {
  const { state, dispatch } = useContext(MainContext);

  React.useEffect(() => {
    get_list(dispatch);
    if (localStorage.getItem("LISTID")) {
      get_question(dispatch, localStorage.getItem("LISTID"));
    }
  }, []);

  return (
    <div>
      <Routes>
        <Route path="exercises" element={<ListExercises />} />
        {state.list_exercise &&
          state.list_exercise.map((list, index) => (
            <>
              <Route
                key={list.id}
                path={`exercises/${list.slug}`}
                element={<ExerciseModule idList={list.id} />}
              />

              {state.list_question &&
                state.list_question.map((question) => (
                  <Route
                    path={`exercises/${list.slug}/${question.id}`}
                    element={<Interface color="#DD9C10" question={question} />}
                  />
                ))}
            </>
          ))}
      </Routes>
    </div>
  );
};
