import React from "react";
import { Route, Routes } from "react-router-dom";
import { Community } from "../template/community/Community";
import Home from "../template/home/Home";
import { ContentRouter } from "./routers/ContentRouter";

import styles from "../template/content/Content.module.css";
import { ExercisesRouter } from "./routers/ExercisesRouter";
import { GamesRouter } from "./routers/GamesRouter";
import { ChallengeRouter } from "./routers/ChallengeRouter";
import { SettingsRouter } from "./routers/SettingsRouter";
import { ProfileRouter } from "./routers/ProfileRouter";
export function ContainerRoutes() {
  return (
    <div>
      <Routes>
        <Route path="community" element={<Community />} />
        <Route path="home" element={<Home />} />
      </Routes>
      <ContentRouter />
      <ExercisesRouter />
      <GamesRouter />
      <ChallengeRouter />
      <SettingsRouter />
      <ProfileRouter />
    </div>
  );
}
