import {
  LOGIN_SUCCESS,
  LOGIN_USER_FAIL,
  LOAD_USER_SUCCESS,
  LOAD_USER_FAIL,
  USER_AUTHENTICATED_SUCCESS,
  USER_AUTHENTICATED_FAIL,
  PASSWORD_RESET_FAIL,
  PASSWORD_RESET_SUCCESS,
  CONFIRM_RESET_PASSWORD_FAIL,
  CONFIRM_RESET_PASSWORD_SUCCESS,
  LOGOUT,
  ACTIVATION_FAIL,
  LOADING,
  LOADING_FAIL,
  ALTER_PROFILE,
  ALTER_PROFILE_FAIL,
} from "../actions/types";

import React, { createContext, useReducer } from "react";
import { checkAuthenticated, load_user } from "../actions/LoginAction";
import { Navigate } from "react-router-dom";

export const AuthContext = createContext();

const initialState = {
  access: localStorage.getItem("access"),
  refresh: localStorage.getItem("refresh"),
  isAuthenticated: null,
  user: null,
  loading: false,
  error: null,
  errorsignup: null,
};

function reducer(state, action) {
  switch (action.type) {
    case USER_AUTHENTICATED_SUCCESS:
      return {
        ...state,

        isAuthenticated: true,
      };

    case LOADING:
      return {
        ...state,
        loading: true,
      };

    case LOADING_FAIL:
      return {
        ...state,
        loading: false,
      };

    case LOGIN_SUCCESS:
      localStorage.setItem("access", action.payload.access);
      localStorage.setItem("refresh", action.payload.refresh);
      return {
        ...state,
        error: null,
        isAuthenticated: true,
        access: action.payload.access,
        refresh: action.payload.refresh,
        loading: false,
      };
    case LOAD_USER_SUCCESS:
      return {
        ...state,

        user: action.payload,
      };

    case ALTER_PROFILE:
      return { ...state, loading: false };

    case ALTER_PROFILE_FAIL:
      return { ...state, loading: false };

    case USER_AUTHENTICATED_FAIL:
      return {
        ...state,
        isAuthenticated: false,
      };
    case LOAD_USER_FAIL:
      return {
        ...state,
        user: null,
      };
    case LOGIN_USER_FAIL:
      return {
        ...state,
        isAuthenticated: false,
        access: null,
        refresh: null,
        user: null,
        error: "Email or Password incorrects",
      };
    case LOGOUT:
      localStorage.removeItem("access");
      localStorage.removeItem("refresh");
      return {
        ...state,
        error: null,
        isAuthenticated: false,
        access: null,
        refresh: null,
        user: null,
      };
    case PASSWORD_RESET_FAIL:
    case PASSWORD_RESET_SUCCESS:
    case CONFIRM_RESET_PASSWORD_FAIL:
    case CONFIRM_RESET_PASSWORD_SUCCESS:
    case ACTIVATION_FAIL:
      return { ...state };

    default:
      return state;
  }
}

export default function AuthProvider({ children }) {
  const [state, dispatch] = useReducer(reducer, initialState);

  return (
    <AuthContext.Provider value={{ state, dispatch }}>
      {children}
    </AuthContext.Provider>
  );
}
