import React from "react";

import styles from "./Content.module.css";
import ReactHtmlParser from "react-html-parser";
export const ContentElement = ({ content }) => {
  return (
    <div className={styles.container}>
      <h1 className={styles.titleElement}>{content.title}</h1>

      <p className={styles.text}>{ReactHtmlParser(content.text)}</p>
    </div>
  );
};
