import React, { useContext } from "react";

import styles from "./Header.module.css";

import profile from "../../assets/profile/profile.png";
import { AuthContext } from "../../reducers/Auth";
import { logout } from "../../actions/LoginAction";
import menu from "../../assets/default/menu.svg";
import menuopen from "../../assets/default/menuopen.svg";
import trofeu from "../../assets/default/trofeu.svg";
import about from "../../assets/default/about.svg";
import settings from "../../assets/default/settings.svg";
import exit from "../../assets/default/exit.svg";

import notification from "../../assets/default/notification.svg";
import { Link, NavLink } from "react-router-dom";
import logosimplific from "../../assets/default/logosimplific.svg";
export const Header = () => {
  const { state, dispatch } = useContext(AuthContext);

  const [menuactive, setMenu] = React.useState(false);

  return (
    <header className={`${styles.header} ${menuactive && styles.activemenu} `}>
      <Link to="profile/performance" className={styles.containerHeader}>
        <div
          style={{ backgroundImage: `url(${state.user && state.user.photo})` }}
          className={styles.photoprofile}
        ></div>
        <div className={styles.profileHeader}>
          <h3>{state.user && state.user.nickname}</h3>
          <div className={styles.miniscore}>
            <img src={trofeu} width="10px" />
            <p>200pt</p>
          </div>
        </div>
      </Link>

      <div className={styles.containerMenu}>
        <Link className={styles.home} to="/home">
          <img width="15px" src={logosimplific} />
        </Link>
        <img src={notification} width="15px" />
        {menuactive ? (
          <img
            onClick={() => {
              setMenu(!menuactive);
            }}
            width="45px"
            src={menuopen}
          />
        ) : (
          <img
            onClick={() => {
              setMenu(!menuactive);
            }}
            width="20px"
            src={menu}
          />
        )}
      </div>
      <ul className={styles.navlink}>
        <NavLink to="settings" hidden={!menuactive}>
          <img width="15px" src={settings} />
        </NavLink>

        <NavLink to="about" hidden={!menuactive}>
          <img width="15px" src={about} />
        </NavLink>
        <li
          hidden={!menuactive}
          onClick={() => {
            logout(dispatch);
          }}
        >
          <img width="15px" src={exit} />
        </li>
      </ul>
    </header>
  );
};
