import React, { useContext } from "react";
import styles from "./Interface.module.css";
import "./interface.css";
import { useNavigate } from "react-router-dom";
import { Rules } from "./rules";
import { MainContext } from "../../context/Context";

import easy from "../../assets/default/easy.svg";
import medium from "../../assets/default/medium.svg";
import hard from "../../assets/default/hard.svg";

export const Interface = ({ question, color }) => {
  const { state, dispatch } = useContext(MainContext);

  const navigate = useNavigate();
  const [ativa, setAtiva] = React.useState(0);
  const [ativa2, setAtiva2] = React.useState(false);

  const [valor, setValor] = React.useState([]);

  const [selected, setSelected] = React.useState();
  const [selectedequivalence, setSelectedEquivalence] = React.useState();
  const [selectedinference, setSelectedInference] = React.useState();
  const [selectedpredicate, setSelectedPredicate] = React.useState();

  const add = (e) => {
    setValor([...valor, e.target.innerText]);
  };

  const activeInterface = (id) => {
    const elements = document.querySelectorAll("");
    console.log("test,id", id.target);
  };

  function remove() {
    let k = [...valor];
    k.pop();
    setValor(k);
  }

  const props = {
    p: ["p  q", "~q", "~q", "~q", "~q"],

    conclusion: " ~p",
  };

  const propscreate = {
    p: ["q  e", "~s", "~b", "~v", "~q"],

    conclusion: " ~p",
  };

  const [listprops, setListProps] = React.useState(propscreate);

  const restart = () => {
    setListProps([]);
  };

  function next() {
    navigate(`/exercises/list-a/${question.id + 1}`);
  }

  return (
    <div className={styles.container} id="init">
      <div className={styles.containerIntern}>
        <div className={styles.statement}>
          <div className={styles.headerQuestion}>
            <p className="">Ex: {question.id}</p>
            <img width="15px" src={easy} />
          </div>

          <p style={{ textAlign: "center" }}>{question.text}</p>
        </div>
        <div className={styles.boxRules}>
          <ul className={styles.buttonBox}>
            <li
              onClick={(e) => {
                setAtiva(2);
              }}
              className={ativa === 2 && "activeInterface"}
            >
              Inference
            </li>
            <li
              onClick={(e) => {
                setAtiva(1);
              }}
              className={ativa === 1 && "activeInterface"}
            >
              Predicate
            </li>
            <li
              id="equivalence"
              onClick={(e) => {
                setAtiva(0);
              }}
              className={ativa === 0 && "activeInterface"}
            >
              Equivalence
            </li>
          </ul>

          <div className={styles.buttonbox3} hidden={ativa != 0}>
            {" "}
            {Rules.equivalence.map((element, index) => (
              <div
                onClick={() => {
                  setSelectedEquivalence(index + 0);
                }}
                id={`${index + 0}`}
                className={`inputselect ${
                  selectedequivalence == index + 0 && "selected"
                }`}
              >
                <span>{index}</span>
                <p>{element.name}:</p>
                <p>{element.method}</p>
                <span>p</span>
              </div>
            ))}
          </div>
          <div className={styles.buttonbox3} hidden={ativa != 1}>
            {" "}
            {Rules.predicate.map((element, index) => (
              <div
                onClick={() => {
                  setSelectedPredicate(index);
                }}
                id={index}
                className={`inputselect ${
                  selectedpredicate == index && "selected"
                }`}
              >
                <span>{index}</span>
                <p>{element.name}:</p>
                <p>{element.method}</p>
                <span>p</span>
              </div>
            ))}
          </div>
          <div className={styles.buttonbox3} hidden={ativa != 2}>
            {Rules.inference.map((element, index) => (
              <div
                onClick={() => {
                  setSelectedInference(index);
                }}
                id={index}
                className={`inputselect ${
                  selectedinference == index && "selected"
                }`}
              >
                <span>{index}</span>
                <p>{element.name}:</p>
                <p>{element.method}</p>
                <span>p</span>
              </div>
            ))}
          </div>
        </div>
        <div hidden={ativa != 1 && ativa != 0} className={styles.button}>
          Selection expresion
        </div>
        <div className={styles.boxConclusion}>
          <ul className={styles.buttonBox2}>
            <li
              onClick={(e) => {
                setAtiva2(true);
              }}
              className={ativa2 === true && "activeInterface"}
            >
              Proof
            </li>
            <li
              onClick={(e) => {
                setAtiva2(false);
              }}
              className={ativa2 === false && "activeInterface"}
            >
              Input
            </li>
            {ativa2 == true && (
              <div className={styles.conclusion}>{props.conclusion}</div>
            )}
            {ativa2 ? (
              ""
            ) : (
              <>
                <div
                  onClick={() => {
                    remove();
                  }}
                  className={styles.buttoninputdelete}
                >
                  Delete
                </div>
                <div className={styles.buttoninputconclusion}>Conclusion</div>
              </>
            )}
          </ul>
          {ativa2 ? (
            <div className={styles.containerboxselect}>
              <div className={styles.boxselect}>
                {props.p.map((x, index) => (
                  <div
                    onClick={() => {
                      setSelected(index);
                    }}
                    id={index}
                    className={`inputselect ${selected == index && "selected"}`}
                  >
                    <span>{index}</span>
                    <p>{x}</p>
                    <span>p</span>
                  </div>
                ))}
                <hr />
                {listprops.p &&
                  listprops.p.map((x, index) => (
                    <div
                      onClick={() => {
                        setSelected(index);
                      }}
                      id={index}
                      className={`inputselect ${
                        selected == index && "selected"
                      }`}
                    >
                      <span>{index}</span>
                      <p>{x}</p>
                      <span>p</span>
                    </div>
                  ))}
              </div>
              <div className={styles.boxbutton}>
                <div
                  onClick={(e) => {
                    add(e);
                  }}
                  className={styles.buttoninput}
                >
                  PROVE
                </div>
                <div
                  onClick={(e) => {
                    restart();
                  }}
                  className={styles.buttoninput}
                >
                  CLEAR
                </div>
                <div
                  onClick={(e) => {
                    add(e);
                    next();
                  }}
                  className={styles.buttoninput}
                >
                  NEXT
                </div>
              </div>
            </div>
          ) : (
            <div>
              <p className={styles.inputText}>{valor}</p>
              <div className={styles.boxbuttoninput}>
                <div
                  onClick={(e) => {
                    add(e);
                  }}
                  className={styles.buttoninput}
                >
                  
                </div>
                <div
                  onClick={(e) => {
                    add(e);
                  }}
                  className={styles.buttoninput}
                >
                  
                </div>
                <div
                  onClick={(e) => {
                    add(e);
                  }}
                  className={styles.buttoninput}
                >
                  
                </div>
                <div
                  onClick={(e) => {
                    add(e);
                  }}
                  className={styles.buttoninput}
                >
                  
                </div>
                <div
                  onClick={(e) => {
                    add(e);
                  }}
                  className={styles.buttoninput}
                >
                  
                </div>
                <div
                  onClick={(e) => {
                    add(e);
                  }}
                  className={styles.buttoninput}
                >
                  ,
                </div>
                <div
                  onClick={(e) => {
                    add(e);
                  }}
                  className={styles.buttoninput}
                >
                  
                </div>
                <div
                  onClick={(e) => {
                    add(e);
                  }}
                  className={styles.buttoninput}
                >
                  
                </div>
                <div
                  onClick={(e) => {
                    add(e);
                  }}
                  className={styles.buttoninput}
                >
                  (
                </div>
                <div
                  onClick={(e) => {
                    add(e);
                  }}
                  className={styles.buttoninput}
                >
                  )
                </div>
                <div
                  onClick={(e) => {
                    add(e);
                  }}
                  className={styles.buttoninput}
                >
                  FN
                </div>
                <div
                  onClick={(e) => {
                    add(e);
                  }}
                  className={styles.buttoninput}
                >
                  VAR
                </div>
                <div
                  onClick={(e) => {
                    add(e);
                  }}
                  className={styles.buttoninput}
                >
                  <select className={styles.buttoninputselect}>
                    <option disabled selected>
                      CST
                    </option>
                    <option
                      onClick={(e) => {
                        add(e);
                      }}
                    >
                      a
                    </option>
                    <option
                      onClick={(e) => {
                        add(e);
                      }}
                    >
                      b
                    </option>
                    <option
                      onClick={(e) => {
                        add(e);
                      }}
                    >
                      c
                    </option>
                    <option
                      onClick={(e) => {
                        add(e);
                      }}
                    >
                      d
                    </option>
                    <option
                      onClick={(e) => {
                        add(e);
                      }}
                    >
                      e
                    </option>
                  </select>
                </div>

                <div
                  onClick={(e) => {
                    add(e);
                  }}
                  className={styles.buttoninput}
                >
                  <select className={styles.buttoninputselect}>
                    <option disabled selected>
                      QNT
                    </option>
                    <option
                      onClick={(e) => {
                        add(e);
                      }}
                    >
                      
                    </option>
                    <option
                      onClick={(e) => {
                        add(e);
                      }}
                    >
                      
                    </option>
                  </select>
                </div>
                <select className={styles.buttoninputselect}>
                  <option disabled selected>
                    FUN
                  </option>

                  <option
                    onClick={(e) => {
                      add(e);
                    }}
                  >
                    p
                  </option>
                  <option
                    onClick={(e) => {
                      add(e);
                    }}
                  >
                    q
                  </option>
                  <option
                    onClick={(e) => {
                      add(e);
                    }}
                  >
                    r
                  </option>
                  <option
                    onClick={(e) => {
                      add(e);
                    }}
                  >
                    s
                  </option>
                  <option
                    onClick={(e) => {
                      add(e);
                    }}
                  >
                    t
                  </option>
                  <option
                    onClick={(e) => {
                      add(e);
                    }}
                  >
                    u
                  </option>
                  <option
                    onClick={(e) => {
                      add(e);
                    }}
                  >
                    v
                  </option>
                  <option
                    onClick={(e) => {
                      add(e);
                    }}
                  >
                    
                  </option>
                  <option
                    onClick={(e) => {
                      add(e);
                    }}
                  >
                    
                  </option>
                </select>
              </div>
            </div>
          )}
        </div>
      </div>
    </div>
  );
};
