import React, { useContext } from "react";
import { Link } from "react-router-dom";

import styles from "./Exercises.module.css";

import write from "../../assets/default/write.svg";
import checked from "../../assets/default/checked.svg";
import checkedtrue from "../../assets/default/checkedtrue.svg";
import { MainContext } from "../../context/Context";
import { get_question } from "../../actions/ExerciseAction";
export const ExerciseModule = ({ idList }) => {
  const [checkedstate, setCheckedTrue] = React.useState(false);
  const { state, dispatch } = useContext(MainContext);
  React.useEffect(() => {
    get_question(dispatch, idList);
  }, []);

  return (
    <div className={styles.container}>
      <div className={styles.boxlistMaster}>
        {state.list_question &&
          state.list_question.map((question) => (
            <Link to={`${question.id}`} className={`${styles.boxlist}`}>
              <img width="15px" src={write} />
              <p>{question.text}</p>
              {checkedstate ? (
                <img width="15px" src={checkedtrue} />
              ) : (
                <img width="15px" src={checked} />
              )}
            </Link>
          ))}
      </div>
    </div>
  );
};
