import React, { useContext } from "react";
import { Link } from "react-router-dom";
import styles from "./Content.module.css";

import content from "../../assets/default/content.svg";
import { MainContext } from "../../context/Context";
import { get_content } from "../../actions/ContentAction";

export const Content = () => {
  const { state, dispatch } = useContext(MainContext);

  React.useEffect(() => {
    get_content(dispatch);
  }, []);

  return (
    <div className={styles.container}>
      {" "}
      <h2 className={styles.title}>Content</h2>
      <div className={styles.boxfixed}>
        {state.content &&
          state.content.map((content, index) => (
            <Link to={`${content.slug}`}>
              <h4>{content.topic}</h4>
              <div className={styles.boxContent}>
                <img src={content.image} />
              </div>
            </Link>
          ))}
      </div>
    </div>
  );
};
