import React from "react";
import { NavLink } from "react-router-dom";
import styles from "./Profile.module.css";

import profile from "../../assets/default/profile.svg";
import performance from "../../assets/default/performance.svg";

export const ProfileNav = () => {
  return (
    <div className={styles.containerNavLink}>
      <h1 className={styles.title}>Profile</h1>
      <ul className={styles.NavLink}>
        <NavLink to="/profile/performance">
          <img width="20px" src={performance} />
        </NavLink>
        <NavLink to="/profile/edit">
          <img width="20px" src={profile} />
        </NavLink>
      </ul>
    </div>
  );
};
